 /*# sourceMappingURL=bootstrap.min.css.map */* {
     box-sizing: border-box;
 }
 a {
     color: #337ab7;
     text-decoration: none;
     background-color: transparent;
 }
 img {
     vertical-align: middle;
     border: 0;
 }
 ol,
 ul {
     margin-top: 0;
     margin-bottom: 10px;
 }
 article,
 aside,
 details,
 figcaption,
 figure,
 footer,
 header,
 hgroup,
 main,
 menu,
 nav,
 section,
 summary {
     display: block;
 }
 :after,
 :before {
     -webkit-box-sizing: border-box;
     box-sizing: border-box;
 }
 html,
 body {
     margin: 0;
     padding: 0;
     font-family: Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif;
     font-size: 16px;
     -webkit-font-smoothing: antialiased;
 }
 body {
     line-height: 1.42857;
     color: #333;
 }
 body.overflow {
     overflow: hidden;
 }
 
 .g-wrapper {
     overflow: hidden;
 }
 .header__wrapper {
     position: relative;
     background-color: #f8f8f8;
 }
 .header__wrapper header {
     position: relative;
     z-index: 1;
     top: 0;
     transition: all .3s ease;
 }
 .header__wrapper header.open {
     top: 0;
     z-index: 999;
     background: transparent;
 }
 .header__policy-header {
     background: #345588 url(./images/images-header.png) center center;
     /*background-size: 100%;*/
     background-size: auto auto;
 }
 .header__policy-header .row {
     margin-left: -10px !important;
     margin-right: -10px !important;
 }
 .header__policy-header .col-lg-12 {
     padding-right: 10px !important;
     padding-left: 10px !important;
 }
 @media (max-width: 767px) {
     .header__policy-header {
         min-height: auto;
         height: 64px;
     }
     .header__policy-header .container:last-child {
         display: none;
     }
 }
 .container {
     margin-right: auto;
     margin-left: auto;
     padding-left: 10px;
     padding-right: 10px;
 }
 @media (max-width: 767px) {
     .container {
         padding-left: 20px;
         padding-right: 20px;
     }
 }
 @media (min-width: 768px) {
     .container {
         width: 740px;
     }
 }
 @media (min-width: 992px) {
     .container {
         width: 960px;
     }
 }
 @media (min-width: 1200px) {
     .container {
         width: 1160px;
     }
 }
 .header__topline {
     margin-top: 50px;
 }
 @media (max-width: 767px) {
     .header__topline {
         margin-top: 20px;
     }
 }
 .header__logo {
     float: left;
 }
 @media (max-width: 767px) {
     .header__logo img {
         width: 160px;
     }
 }
 .header__hamburger {
     width: 25px;
     height: 20px;
     float: right;
     margin-top: 4px;
     position: relative;
 }
 .visible-xs {
     display: none!important;
 }
 @media (max-width: 767px) {
     .visible-xs {
         display: block!important;
     }
 }
 .header__hamburger span {
     display: block;
     width: 25px;
     height: 2px;
     background-color: #fff;
     position: absolute;
     opacity: 1;
     -webkit-transition: all .3s ease;
     transition: all .3s ease;
 }
 .header__hamburger span:nth-child(1) {
     top: 0;
 }
 .header__hamburger span:nth-child(2) {
     top: 8px;
     left: 0;
 }
 .header__hamburger span:nth-child(3) {
     top: 16px;
 }
 .header__hamburger.open span:nth-child(1) {
     -webkit-transform: rotate(135deg);
     transform: rotate(135deg);
     top: 8px;
 }
 .header__hamburger.open span:nth-child(2) {
     opacity: 0;
     left: -20px;
 }
 .header__hamburger.open span:nth-child(3) {
     -webkit-transform: rotate(-135deg);
     transform: rotate(-135deg);
     top: 8px;
 }
 .header__nav {
     float: right;
 }
 @media (max-width: 767px) {
     .header__nav {
         display: none;
     }
 }
 .footer-menubar ul,
 .menubar ul {
     list-style-type: none;
     margin: 0;
     padding: 0;
     font-size: 0;
 }
 .footer-menubar ul li,
 .menubar ul li {
     font-size: 0;
     display: inline-block;
     margin-left: 40px;
     line-height: 28px;
 }
 .footer-menubar ul li:first-child,
 .menubar ul li:first-child {
     margin-left: 0;
 }
 .footer-menubar ul li a:link,
 .footer-menubar ul li a:visited,
 .menubar ul li a:link,
 .menubar ul li a:visited {
     display: inline-block;
     color: #fff;
     font-size: 16px;
     font-size: 1rem;
     font-weight: 400;
     -webkit-transition: all .3s ease;
     transition: all .3s ease;
     text-decoration: none;
 }
 .footer-menubar ul li a:link:after,
 .footer-menubar ul li a:visited:after,
 .menubar ul li a:link:after,
 .menubar ul li a:visited:after {
     content: "";
     display: block;
     width: 0%;
     height: 2px;
     background-color: #fec902;
     -webkit-transition: all .3s ease;
     transition: all .3s ease;
 }
 .footer-menubar ul li a:link:hover:after,
 .footer-menubar ul li a:visited:hover:after,
 .menubar ul li a:link:hover:after,
 .menubar ul li a:visited:hover:after {
     width: 100%;
 }
 .footer-menubar ul li.active a:link:after,
 .footer-menubar ul li.active a:visited:after,
 .menubar ul li.active a:link:after,
 .menubar ul li.active a:visited:after {
     width: 100%;
 }
 .header__title {
     margin: 45px 0;
     font-weight: 700;
     font-size: 50px;
     line-height: 60px;
 }
 @media (max-width: 991px) {
     .header__title {
         font-size: 30px;
     }
 }
 .header__effective-date {
     margin-bottom: 35px;
     font-size: 16px;
 }
 .header__effective-date,
 .header__title {
     padding: 0 10px;
     color: #fff;
 }
 .header__hidden .header__title,
 .header__hidden .header__effective-date {
     padding: 0;
     color: #153d69 !important;
     font-weight: 700 !important;
 }
 .header__hidden .header__title {
     margin: 40px 0 25px 0;
     line-height: normal;
     font-size: 45px;
 }
 
 @media(min-width: 768px) {
     .header__hidden {
         display: none;
     }
 }
 
 @media(max-width: 768px) {
     .header__hidden .header__title{
         margin-top:10px;
     font-size: 30px;
     }
 }
 
 
 /*-----------footer---------*/
 .footer__wrapper {
     background-color: #253858;
 }
 .footer__wrapper footer {
     border-top: 1px solid #7b899b;
 }
 .footer__topline {
     padding-top: 35px;
     position: relative;
     margin-bottom: 0;
 }
 @media (max-width: 767px) {
     .footer__topline {
         padding-top: 25px;
         margin-bottom: 0;
     }
 }
 .footer__bottomline {
     padding-bottom: 40px;
 }
 @media (max-width: 767px) {
     .footer__bottomline {
         padding-bottom: 25px;
     }
 }
 .footer__copy {
     display: -webkit-box;
     display: -ms-flexbox;
     display: flex;
     -webkit-box-orient: horizontal;
     -webkit-box-direction: normal;
     -ms-flex-direction: row;
     flex-direction: row;
 }
 @media (max-width: 767px) {
     .footer__copy {
         -webkit-box-pack: center;
         -ms-flex-pack: center;
         justify-content: center;
     }
 }
 .footer__copy p {
     font-size: 12px;
     font-size: .75rem;
     color: #fff;
     font-weight: 300;
     margin: 0;
     margin-top: 5px;
     line-height: 18px;
     padding: 0;
 }
 @media (max-width: 767px) {
     .footer__copy p {
         text-align: center;
         font-size: 12px;
         font-size: .75rem;
         opacity: .4;
     }
 }
 .footer__copy .copy-links {
     display: -webkit-box;
     display: -ms-flexbox;
     display: flex;
     flex-direction: row;
 }
 .footer__copy .copy-links .copy-link {
     display: inline-block;
     margin-top: 6px;
     padding: 0 10px;
     border-right: 1px solid #fff;
     font-size: 12px;
     color: #fff;
     text-decoration: none;
 }
 .footer__copy .copy-links .copy-link:last-child {
     padding-right: 0;
     border-right: none;
 }
 @media (max-width: 575px) {
     .footer__copy {
         flex-direction: column;
     }
     .footer__copy p {
         order: 2;
     }
     .footer__copy .copy-links {
         flex-direction: column;
         justify-content: center;
         order: 1;
     }
     .footer__copy .copy-links .copy-link {
         margin: 5px 0;
         padding: 0;
         border-right: none;
         text-align: center;
     }
     .footer__copy .copy-links .copy-link:first-child {
         margin-top: 0;
     }
     .footer__copy .copy-links .copy-link:last-child {
         margin-bottom: 10px;
     }
 }
 
 /*-----article-list-------*/
 .article-list {
     display: flex;
     justify-content: space-between;
 }
 .article-list__wrapper {
     width: 100%;
 }
 .blog-post__link:hover {
     text-decoration: none;
 }
 .blog-post__main-img-wrapper {
     transform: scale(1.0);
     opacity: .8;
     transition: transform 200ms, opacity 200ms;
 }
 .blog-post__link:hover .blog-post__main-img-wrapper {
     transform: scale(1.05);
     opacity: 1;
     transition: transform 200ms, opacity 200ms;
 }
 @media(min-width: 992px) {
     .article-list__wrapper {
         width: calc(100% - 360px);
     }
     .blog-post__link {
         display: block;
         margin-top: 30px;
         width: 100%;
         /*border-bottom: 1px dotted #c8cbd0;*/
     }
     .blog-post__main-img {
         width: 100%;
         height: 310px;
         overflow: hidden;
         display: block;
     }
     .blog-post__link .blog-post__main-img-wrapper img {
         width: 100%;
         height: 100%;
         transform: translateY(calc(-50% + 210px));
     }
     .blog-post__date {
         margin: 28px 0 15px 33px;
         color: #b9bdc3;
         font-size: 13px;
         text-transform: uppercase;
     }
     .blog-post__announcement {
         margin-bottom: 30px;
         padding-left: 30px;
         box-sizing: border-box;
         border-left: 3px solid #73d141;
     }
     .blog-post__title {
         color: #153d69;
         font-size: 30px;
         line-height: 28px;
         font-weight: 600;
     }
     .blog-post__description {
         margin-top: 25px;
         color: #414143;
         font-size: 16px;
     }
     .blog-post__border-dotted-spaced {
         padding: 1px 0;
         background-image: linear-gradient(to right, #c8cbd0 10%, rgba(255, 255, 255, 0) 0%);
         background-position: top;
         background-size: 10px 1px;
         background-repeat: repeat-x;
     }
 }
 @media(max-width: 991px) {
     .blog-post__main-img {
         width: 100%;
         height: 390px;
         overflow: hidden;
         display: block;
     }
     .blog-post__link {
         display: block;
         margin-top: 15px;
     }
     .blog-post__date {
         margin: 10px 0 10px 13px;
         color: #b9bdc3;
         font-size: 13px;
         text-transform: uppercase;
     }
     .blog-post__announcement {
         margin-bottom: 30px;
         padding-left: 10px;
         box-sizing: border-box;
         border-left: 3px solid #73d141;
     }
     .blog-post__title {
         margin-bottom: -12px;
         color: #153d69;
         font-size: 24px;
         line-height: 26px;
         font-weight: 600;
     }
     .blog-post__description {
         margin-top: 25px;
         color: #414143;
         font-size: 16px;
     }
     .blog-post__link .blog-post__main-img-wrapper img {
         width: 100%;
         height: 100%;
     }
 }
 @media(max-width: 767px) {
     .blog-post__main-img {
         width: 100%;
         height: calc((100vw - 40px) / 1.857);
     }
 }
 @media(min-width: 1199px) {
     .blog-post__main-img {
         width: 100%;
         height: 420px;
         overflow: hidden;
         display: block;
     }
 }
 
 /*-----pagination-----*/
 nav.pagination {
     display: flex;
     justify-content: center;
     width: 100%;
 }
 nav.pagination .nav-links {
     display: inline-block;
 }
 .page-numbers {
     display: inline-block;
     padding: 5px 10px;
     box-shadow: none;
     margin: 0 2px;
     background: #fff;
     border-radius: 3px;
     font-size: 13px;
     font-weight: 600;
     line-height: 20px;
     color: #282828;
     text-align: center;
     transition: background 200ms, color 200ms;
 }
 nav.pagination .page-numbers:first-child {
     margin-left: 0;
 }
 nav.pagination .page-numbers:last-child {
     margin-right: 0;
 }
 .page-numbers:hover,
 .page-numbers:focus,
 .page-numbers:active {
     text-decoration: none;
     color: #282828;
 }
 .page-numbers:not(.prev):not(.next) {
     width: 30px;
     height: 30px;
     border-radius: 50%;
     padding: 5px;
 }
 .page-numbers:not(.prev):not(.next):not(.dots):hover,
 .page-numbers.current {
     background: #73d141;
     color: #fff;
     transition: background 200ms, color 200ms;
 }
 .prev.page-numbers,
 .next.page-numbers {
     color: #153d69;
     letter-spacing: 2px;
 }
 .prev.page-numbers {
     padding-left: 0;
 }
 .next.page-numbers {
     padding-right: 0;
 }
 .prev.page-numbers:before,
 .next.page-numbers:after {
     content: '';
     display: inline-block;
     margin-bottom: -1px;
     width: 30px;
     height: 11px;
     background: url(./images/images-pagination-arrow.png) no-repeat;
     background-size: auto 100%;
 }
 .next.page-numbers:after {
     transform: rotate(180deg);
 }
 @media(max-width: 991px) {
     .page-numbers {
         margin: 0;
         padding: 5px 5px;
     }
     .prev.page-numbers:before,
     .next.page-numbers:after {
         width: 20px;
     }
 }
 
 
 
 
 
 
 
 /*------article-------*/
 .article {
     margin: 0 auto;
     color: #414143;
     line-height: 25px;
     font-size: 16px;
 }
 .article__wrapper {
     display: flex;
     justify-content: space-between;
     padding: 0 15px;
     /*background: lightblue;*/
 }
 @media(max-width: 767px) {
     .article__wrapper {
         padding: 0 15px;
     }
 }
 .article__only {
     width: 100%;
     padding-top: 30px;
 }
 @media(min-width: 992px) {
     .article__only {
         width: calc(100% - 360px);
     }
 }
 .article h2 {
     margin: 30px 0 25px 0;
     line-height: normal;
     font-size: 30px;
 }
 .article h3 {
     margin: 24px 0 19px 0;
     font-size: 24px;
 }
 .article p {
     margin: 25px 0;
     text-align: justify;
 }
 .article blockquote p{
     margin: 0;
 }
 
 .article a {
     color: #3659aa;
     font-weight: 400;
 }
 .article a:active,
 .article a:focus,
 .article a:hover {
     color: #b30;
     text-decoration: underline;
 }
 .article ul {
     margin: 25px 0;
     padding-left: 15px;
     list-style-type: none;
 }
 .article ul li {
     position: relative;
     margin: 10px 0;
 }
 .article ul li:before {
     content: '';
     position: absolute;
     top: 9px;
     left: -15px;
     display: block;
     width: 6px;
     height: 6px;
     border-radius: 50%;
     background-color: #153d69;
 }
 .article ol {
     margin: 25px 0;
     padding-left: 15px;
 }
 .article ol {
     margin: 25px 0;
     padding-left: 15px;
     list-style-type: none;
     counter-reset: li
 }
 .article ol li {
     position: relative;
     margin: 10px 0;
     padding-left: 5px;
     counter-increment: li;
 }
 .article ol li::before {
     content: '.' counter(li);
     color: #153d69;
     position: absolute;
     top: 0;
     left: -15px;
     display: block;
     text-align: right;
     direction: rtl;
     font-weight: 700;
 }
 .article__only div {
     width: 100% !important;
 }
 .article__only img {
     width: 100%;
     height: 100%;
 }
 .article__only img + p {
     margin-top: 10px;
     margin-left: 10px;
     margin-right: 10px;
     font-style: italic;
     font-size: 14px;
     text-align: center;
 }
 
